import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';

import { MatSelectionList, MatListOption} from '@angular/material/list';

@Component({
  selector: 'app-panel-list',
  templateUrl: './panel-list.component.html',
  styleUrls: ['./panel-list.component.scss']
})
export class PanelListComponent implements OnInit {

  filteredValues:any = [];


  @Input() fieldList:any;
  @Input() selectedItem:any;
  @Input() fieldValue:string;

  @Output() public outputParams = new EventEmitter();


  constructor() { }

  filterValues(event): void
  {
      // Get the value
      const value = event.target.value.toLowerCase();

      // Filter the tags
      this.filteredValues = this.fieldList.filter(data => data[this.fieldValue].toString().toLowerCase().includes(value));
  }

  filterValuesInputKeyDown(event): void {
      // Return, if the pressed key is not 'Enter'
      if ( event.key !== 'Enter' )
      {
          return;
      }

  }

  onListSelect() {
    this.outputParams.emit({
      selectedItem: this.selectedItem
    });
  }

  ngOnInit(): void {

    if(this.fieldList != undefined){
      this.filteredValues = this.fieldList;
    }
    
  }

  ngOnChanges(changes): void {
  }

}
