import { Component, OnInit, Input, ViewEncapsulation, ViewChild, ElementRef, TemplateRef, ViewContainerRef, Output, EventEmitter, OnChanges } from '@angular/core';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { ControlContainer, NgForm } from '@angular/forms';


@Component({
  selector: 'learn-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class SelectComponent implements OnInit {

  fieldName:string = "";

  selectedItem = [];
  selectedModel:string = "";

  @Input() fieldRequired:string;
  @Input() labelText:string;
  @Input() fieldPlaceholder:string;
  @Input() fieldList:any;
  @Input() fieldValue:string;
  @Input() isDisabled?:boolean;
  @Input() fieldModel?:any;
  @Input() fieldId:any;
  @Input() isLabel?:any;
  @Input() toolTip?:string;
  @Input() isClear?:boolean = true;

  @Output() public fieldParams = new EventEmitter();

  // Private
  private _selectPanelOverlayRef: OverlayRef;

  @ViewChild('selectPanel')
  private _tagsPanel: TemplateRef<any>;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    private _element:ElementRef
  ) { }

  isFieldRequired(){
    return this.fieldRequired == "required" ? true : false;
  }

  getFieldModel(event){
    this.fieldModel = event.selectedItem[0][this.fieldValue]
    this.fieldParams.emit(event.selectedItem)
    this.selectedItem = event.selectedItem;
    this._selectPanelOverlayRef.detach();
  }


  isValue() {
    return this.selectedModel != '' ? true : false
  }

  isFieldValue(){
    return (this.selectedModel != null && this.fieldModel != undefined) ? true : false;
  }

  isTooltip(){
    return this.toolTip != undefined ? true : false
  }

  clearSelection(){

    this.selectedModel = "";

    const newObj = Object.keys(this.selectedItem[0]).reduce(
      (accumulator, current) => {
        accumulator[current] = null;
        return accumulator
    }, {});

    var selectedItem = [newObj];
    this.selectedItem = [newObj];
    this.fieldParams.emit(selectedItem)
  }


  OpenDropDown() {

    // Create the overlay
    this._selectPanelOverlayRef = this._overlay.create({
      backdropClass   : '',
      hasBackdrop     : true,
      scrollStrategy  : this._overlay.scrollStrategies.block(),
      positionStrategy: this._overlay.position()
                            .flexibleConnectedTo(this._element.nativeElement)
                            .withFlexibleDimensions()
                            .withViewportMargin(0)
                            .withLockedPosition()
                            .withPositions([
                                {
                                    originX : 'start',
                                    originY : 'bottom',
                                    overlayX: 'start',
                                    overlayY: 'top'
                                }
                            ])
    });


    // Create a portal from the template
    const templatePortal = new TemplatePortal(this._tagsPanel, this._viewContainerRef);

    // Attach the portal to the overlay
    this._selectPanelOverlayRef.attach(templatePortal);

    let cdks = document.querySelectorAll('.cdk-overlay-backdrop');

    [].forEach.call(cdks, function(el, i) {
      el.style.zIndex = parseInt("100000")+i;
      if(i==0){
        el.style.opacity = 1
      }
      else {
        el.style.opacity = 0
      }
    });

    // Subscribe to the backdrop click
    this._selectPanelOverlayRef.backdropClick().subscribe(() => {

        // If overlay exists and attached...
        if ( this._selectPanelOverlayRef && this._selectPanelOverlayRef.hasAttached() )
        {
            // Detach it
            this._selectPanelOverlayRef.detach();

        }

        // If template portal exists and attached...
        if ( templatePortal && templatePortal.isAttached )
        {
            // Detach it
            templatePortal.detach();
        }
    });

  }

  camelCase(str) {
    return str.replace(/(?:^\w|[A-Z]|\b\w)/g, (word, index) => {
      return index === 0 ? word.toLowerCase() : word.toUpperCase();
    }).replace(/\s+/g, '');
  }

  getLabelText(text){
    return text.replace(/[0-9]/g, '');
  }

  ngOnInit(): void {
    this.fieldName = this.camelCase(this.labelText)
  }

  ngOnChanges(): void {

    if((this.fieldModel != undefined && this.fieldModel != "" && this.fieldModel != null) && this.fieldList != undefined){
      this.selectedItem = this.fieldList.filter( item => { return item[this.fieldId] == this.fieldModel } )
      if(this.selectedItem.length != 0)
        this.selectedModel = this.selectedItem[0][this.fieldValue]
    }
    else {
      this.selectedModel = ""
      this.selectedItem = [];
    }

  }


}
